"use client";

import Link from "next/link";
import { useMemo, useState } from "react";
import { usePatients } from "@/hooks/use-patients";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { EmptyState, ErrorState, LoadingState } from "@/components/shared/data-states";
import type { PatientStatus } from "@/types/patient";

export default function PatientsListPage() {
  const [search, setSearch] = useState("");
  const [status, setStatus] = useState<PatientStatus | "all">("all");
  const [sort, setSort] = useState<"risk_desc" | "name_asc">("risk_desc");

  const filters = useMemo(
    () => ({
      search,
      status: status === "all" ? undefined : status,
      sort,
    }),
    [search, status, sort],
  );

  const { data, loading, error, reload } = usePatients(filters);

  if (loading) {
    return <LoadingState label="Carregando pacientes" />;
  }

  if (error) {
    return (
      <ErrorState
        description="Não foi possível carregar a lista de pacientes."
        onRetry={() => {
          void reload();
        }}
      />
    );
  }

  return (
    <main className="mx-auto flex w-full max-w-6xl flex-col gap-5">
      <header className="kv-surface rounded-3xl p-6">
        <div className="flex items-start justify-between gap-3">
          <div>
            <p className="kv-pill inline-flex">PRO-02</p>
            <h1 className="mt-3 text-2xl font-semibold tracking-tight">Lista de Pacientes</h1>
            <p className="mt-2 text-sm text-muted-foreground">
              Gestão de pacientes com busca, filtros e priorização por risco.
            </p>
          </div>
          <Link href="/patients/new">
            <Button>Novo Paciente</Button>
          </Link>
        </div>
      </header>

      <Card>
        <CardContent className="pt-6">
          <div className="grid gap-3 md:grid-cols-4">
            <Input
              placeholder="Buscar paciente"
              value={search}
              onChange={(event) => setSearch(event.target.value)}
            />

            <select
              className="h-10 rounded-xl border border-input/80 bg-card/70 px-3 text-sm"
              value={status}
              onChange={(event) => setStatus(event.target.value as PatientStatus | "all")}
            >
              <option value="all">Todos os status</option>
              <option value="active">Ativo</option>
              <option value="at_risk">Em risco</option>
              <option value="paused">Pausado</option>
              <option value="inactive">Inativo</option>
            </select>

            <select
              className="h-10 rounded-xl border border-input/80 bg-card/70 px-3 text-sm"
              value={sort}
              onChange={(event) => setSort(event.target.value as "risk_desc" | "name_asc")}
            >
              <option value="risk_desc">Risco (maior primeiro)</option>
              <option value="name_asc">Nome (A-Z)</option>
            </select>

            <Button variant="outline" onClick={() => { void reload(); }}>
              Atualizar
            </Button>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Pacientes</CardTitle>
        </CardHeader>
        <CardContent>
          {data && data.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="min-w-full text-sm">
                <thead>
                  <tr className="border-b border-border/70 text-left text-muted-foreground">
                    <th className="px-2 py-2 font-medium">Nome</th>
                    <th className="px-2 py-2 font-medium">Status</th>
                    <th className="px-2 py-2 font-medium">Risco</th>
                    <th className="px-2 py-2 font-medium">Protocolo</th>
                    <th className="px-2 py-2 font-medium">Próx. Check-in</th>
                    <th className="px-2 py-2 font-medium">Ações</th>
                  </tr>
                </thead>
                <tbody>
                  {data.map((patient) => (
                    <tr key={patient.id} className="border-b border-border/40">
                      <td className="px-2 py-3 font-medium">{patient.name}</td>
                      <td className="px-2 py-3 capitalize">{patient.status.replace("_", " ")}</td>
                      <td className="px-2 py-3">{patient.riskScore}</td>
                      <td className="px-2 py-3">{patient.activeProtocolName ?? "—"}</td>
                      <td className="px-2 py-3">{patient.nextCheckinLabel ?? "—"}</td>
                      <td className="px-2 py-3">
                        <div className="flex gap-2">
                          <Link href={`/patients/${patient.id}`}>
                            <Button size="sm" variant="outline">
                              Perfil
                            </Button>
                          </Link>
                          <Link href={`/patients/${patient.id}/messages`}>
                            <Button size="sm" variant="ghost">
                              Mensagem
                            </Button>
                          </Link>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <EmptyState
              title="Nenhum paciente encontrado"
              description="Ajuste os filtros ou adicione um novo paciente para começar."
              ctaLabel="Adicionar paciente"
              onCta={() => {
                window.location.href = "/patients/new";
              }}
            />
          )}
        </CardContent>
      </Card>
    </main>
  );
}
