"use client";

import { useCallback, useEffect, useState } from "react";

type ResourceState<T> = {
  data: T | null;
  loading: boolean;
  error: string | null;
};

export function useResource<T>(loader: () => Promise<T>, dependencyKey = "default") {
  const [state, setState] = useState<ResourceState<T>>({
    data: null,
    loading: true,
    error: null,
  });

  const reload = useCallback(async () => {
    setState((previous) => ({ ...previous, loading: true, error: null }));
    try {
      const data = await loader();
      setState({ data, loading: false, error: null });
    } catch (error) {
      setState({
        data: null,
        loading: false,
        error: error instanceof Error ? error.message : "Erro desconhecido",
      });
    }
  }, [loader]);

  useEffect(() => {
    let isActive = true;

    const run = async () => {
      await Promise.resolve();
      if (!isActive) return;

      setState((previous) => ({ ...previous, loading: true, error: null }));

      try {
        const data = await loader();
        if (!isActive) return;
        setState({ data, loading: false, error: null });
      } catch (error) {
        if (!isActive) return;
        setState({
          data: null,
          loading: false,
          error: error instanceof Error ? error.message : "Erro desconhecido",
        });
      }
    };

    void run();

    return () => {
      isActive = false;
    };
  }, [loader, dependencyKey]);

  return {
    ...state,
    reload,
  };
}
